import React from 'react';
import ReactDOM from 'react-dom';
import { SceneView, Scene } from 'react-sceneview';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button';

import { Map } from '@esri/react-arcgis';
import BermudaTriangle from './BermudaTriangle';
import MyFeatureLayer from './MyFeatureLayer';

ReactDOM.render(
  // <Modal>

  // </Modal>
  <Map
    mapProperties={{ basemap: 'satellite' }}
    viewProperties={{
        center: [-70, 25],
        zoom: 4
    }}>
    <BermudaTriangle />
    <MyFeatureLayer
      featureLayerProperties={{
        url: 'https://services.arcgis.com/P3ePLMYs2RVChkJx/arcgis/rest/services/World_Time_Zones/FeatureServer/0'
      }}
    >
    </MyFeatureLayer>
  </Map>,
  document.getElementById('root')
);

ReactDOM.render(
  <SceneView id="sceneview">
    <Scene portalItem={{ id: 'fa27d45c8323413893daaec626561a67' }} />
  </SceneView>,
  document.getElementById('3dMap')
)